.popup__container {
  width: 430px;
  background-color: white;
  display: flex;
  flex-direction: column;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  position: relative;
  border-radius: 10px;
}

@media screen and (max-width: 550px) {
  .popup__container {
    max-width: 282px;
  }
}
